.site-nav{position:fixed;top:0;left:0;right:0;z-index:50;background:linear-gradient(90deg, rgba(10,6,20,0.92), rgba(10,6,20,0.7));backdrop-filter:blur(8px);border-bottom:1px solid rgba(155,89,255,0.06);padding:0.6rem 0 1rem 0;box-shadow:0 8px 32px rgba(155,89,255,0.15);overflow:visible;width:100vw;margin-left:calc(-50vw + 50%)}
.nav-inner{display:flex;align-items:center;justify-content:space-between;padding:0.4rem 1rem;overflow:visible}
.brand{display:flex;align-items:center}
.brand-inner{position:relative;padding:0.4rem 0.8rem;border-radius:8px;overflow:hidden;cursor:default;display:flex;align-items:center;gap:0.2rem}
.brand-bg{position:absolute;inset:0;background:linear-gradient(135deg,rgba(155,89,255,0.12),rgba(106,13,173,0.08));border:1px solid rgba(155,89,255,0.08);border-radius:inherit;backdrop-filter:blur(8px)}

/* Main brand text with glitch effect */
.brand-text{position:relative;z-index:1}
.glitch-text{font-family:'Poppins',system-ui;font-weight:800;font-size:1.35rem;color:#fff;position:relative;letter-spacing:-0.03em;animation:textShine 4s linear infinite}

/* Glitch effect layers */
.glitch-text::before,
.glitch-text::after{
  content:attr(data-text);
  position:absolute;
  left:0;
  top:0;
  width:100%;
  height:100%;
  background:linear-gradient(90deg,#fff,#9B59FF);
  background-clip:text;
  -webkit-background-clip:text;
  -webkit-text-fill-color:transparent;
  clip-path:polygon(0 0, 100% 0, 100% 45%, 0 45%);
  transform:translateX(0);
  animation:glitchTop 2s ease-in-out infinite alternate-reverse}

.glitch-text::after{
  clip-path:polygon(0 45%, 100% 45%, 100% 100%, 0 100%);
  animation:glitchBottom 2.4s ease-in-out infinite alternate-reverse}

/* Floating Dev text with glow */
.brand-accent{position:relative;z-index:1;display:flex;align-items:center}
.float-text{font-family:'Poppins',system-ui;font-weight:700;font-size:0.8rem;color:var(--accent);position:relative;animation:float 3s ease-in-out infinite}
.glow{position:absolute;width:100%;height:100%;background:radial-gradient(circle at 50% 50%, var(--accent), transparent 70%);opacity:0.2;filter:blur(8px);animation:pulse 3s ease-in-out infinite}

/* Animation keyframes */
@keyframes textShine {
  0%, 100% { opacity: 0.95 }
  50% { opacity: 1 }
}

@keyframes glitchTop {
  0% { transform: translateX(-2px) }
  50% { transform: translateX(2px) }
  100% { transform: translateX(-2px) }
}

@keyframes glitchBottom {
  0% { transform: translateX(2px) }
  50% { transform: translateX(-2px) }
  100% { transform: translateX(2px) }
}

@keyframes float {
  0%, 100% { transform: translateY(0) }
  50% { transform: translateY(-3px) }
}

@keyframes pulse {
  0%, 100% { opacity: 0.2; transform: scale(1) }
  50% { opacity: 0.3; transform: scale(1.1) }
}

/* Hover effects intensify animations */
.brand-inner:hover .glitch-text::before { animation: glitchTop 0.4s steps(2) infinite; }
.brand-inner:hover .glitch-text::after { animation: glitchBottom 0.5s steps(2) infinite; }
.brand-inner:hover .brand-bg { background: linear-gradient(135deg,rgba(155,89,255,0.18),rgba(106,13,173,0.12)); }
.brand-inner:hover .glow { animation: pulse 1.5s ease-in-out infinite; }
.site-nav nav a{color:var(--muted);margin-left:1rem;text-decoration:none;font-weight:700;padding:0.5rem 0.85rem;border-radius:8px;display:inline-block;font-family:'Poppins',Inter,system-ui;font-size:1rem;transition:color 0.3s ease, background 0.3s ease, transform 0.2s ease, filter 0.3s ease}
.site-nav nav a:hover{color:#fff;background:rgba(155,89,255,0.06);transform:translateY(-2px)}
.site-nav nav a.active{color:#fff;background:linear-gradient(90deg,var(--accent),var(--accent-2));filter:drop-shadow(0 10px 25px rgba(155,89,255,0.5));transform:translateY(-2px);font-weight:800;box-shadow:inset 0 1px 0 rgba(255,255,255,0.2)}
.site-nav nav a:focus{outline:2px solid rgba(155,89,255,0.18);outline-offset:2px}

/* glass highlight */
.site-nav nav{position:relative;display:flex;align-items:center}
.nav-highlight{position:absolute;top:6px;left:0;height:42px;border-radius:10px;background:linear-gradient(90deg,rgba(155,89,255,0.2),rgba(106,13,173,0.2));box-shadow:none;pointer-events:none;transition:left 0.35s cubic-bezier(0.34, 1.56, 0.64, 1), width 0.35s cubic-bezier(0.34, 1.56, 0.64, 1), opacity 0.35s ease;transform:translateY(0);opacity:0;z-index:0}
.site-nav nav a.active{color:#fff}
/* ensure links render above the highlight */
.site-nav nav a{position:relative;z-index:2}

@media (max-width:600px){
  .site-nav nav a{margin-left:.7rem;font-size:1rem;padding:0.6rem 0.8rem}
  .brand{font-size:1.2rem}
  .glitch-text{font-size:1.3rem}
}
